  @font-face {
    font-family: 'Spoqa-Han-Sans';
    src: url('./fonts/Spoqa\ Han\ Sans\ Neo\ Light.otf') format('opentype');
    font-style: normal;
    font-weight: 100;
  }
  
  @font-face {
    font-family: 'Spoqa-Han-Sans';
    src: url('./fonts/Spoqa\ Han\ Sans\ Neo\ Regular.otf') format('opentype');
    font-style: normal;
    font-weight: 400;
  }
  
  @font-face {
    font-family: 'Spoqa-Han-Sans';
    src: url('./fonts/Spoqa\ Han\ Sans\ Neo\ Bold.otf') format('opentype');
    font-style: normal;
    font-weight: 700;
  }

  @font-face {
    font-family: 'Jalnan';
    src: url('./fonts/JalnanOTF.otf') format('opentype');
    font-style: normal;
    font-weight: 400;
  }
  
  html {
    font-size: 10px;
  }
  
  body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  }

  